import { ReactComponent as Image } from "../curriculum-vitae-resume-svgrepo-com.svg";
const Display = ({ d, ind, deletes, edit }) => {
  console.log(d);
  return (
    <table>
      <thead>
        <tr>
          <th>First name</th>
          <th>Last Name</th>
          <th>DOB</th>
          <th>Designation</th>
          <th>Image</th>
          <th>Experience</th>
          <th>Delete</th>
          <th>Edit</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{d.fn}</td>

          <td>{d.ln}</td>
          <td>{d.dob}</td>
          <td>{d.dsg}</td>
          <td>
            <Image style={{ width: "2rem" }} />
          </td>
          <td>{d.exp}</td>
          <td>
            <button onClick={() => deletes(ind)}>Delete</button>
          </td>
          <td>
            <button onClick={() => edit(ind)}>Edit</button>
          </td>
        </tr>
      </tbody>
    </table>
  );
};
export default Display;
